<template>
<div>
  <el-dropdown @command = 'handleCommand' trigger = 'click'>
    <el-button @click = 'isTriangleUp = !isTriangleUp'>
      <span>{{curType.title}}</span>
      <div :class = triangle[isTriangleUp]></div>
    </el-button>

    <el-dropdown-menu slot = 'dropdown'>
      <template v-for = '(it, index) in dropData'>
        <el-dropdown-item class = 'dropdownItem' :command = it :key = index>
          {{it.title}}
        </el-dropdown-item>
      </template>
    </el-dropdown-menu>
  </el-dropdown>
</div>
</template>

<script>
export default {
  name: 'dropDown',

  props: {
    dropData: Array
  },

  data() {
    return {
      curType: this.dropData[0],
      isTriangleUp: false,
      triangle: {true: 'triangleUp', false: 'triangleDown'},
    }
  },

  methods: {
    handleCommand (value) {
      this.curType = value;
      this.isTriangleUp = false;
      this.$emit('dropChange', value)
    },
  }
}
</script>

<style scoped>
.el-dropdown .el-button {
  width: 120px;
  height: 40px;
  text-align: left;
}
.el-button span {
  font-size: 18px;
}
.dropdownItem {
  width: 80px;
  font-size: 18px;
}
.triangleUp {
  width: 0;
  height: 0;
  float: right;
  margin-top: 5px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #dcdfe6;
}
.triangleDown {
  width: 0;
  height: 0;
  float: right;
  margin-top: 5px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 10px solid #dcdfe6;
}
</style>



