
function doubleOpen(underFrame,width, height, number) {
  console.log(width + ','+height)
  let bFold = (width - 90) / 2;             //B板折完

  let bWidth = bFold + 68;                  //B板裁完
  let aWidth = bWidth + 25;                 //A板裁完
  let cWidth = bFold + 120;                 //C板裁完
  let dWidth = cWidth - 77;                 //D板裁完

  let acHeight = !underFrame? height - 31 : height - 65;   //有无底框
  let bdHeight = acHeight - 3;

  return [
    [
      {title: '尺寸', content: width + ' * ' + height},
      {title: '数量', content: number},
      {title: '下框', content: !underFrame? '无' : '有'}
    ],
    [
      {title: 'A', content: aWidth + ' * ' + acHeight + ' * ' + number},
      {title: 'B', content: bWidth + ' * ' + bdHeight + ' * ' + number},
      {title: 'C', content: cWidth + ' * ' + acHeight + ' * ' + number},
      {title: 'D', content: dWidth + ' * ' + bdHeight + ' * ' + number},
    ]
  ]

}

export { doubleOpen }



