<template>
<div>
  <header>
    <div class = 'content'>
      <span>种类：</span>
      <dropDown class = 'typeDrop' :dropData = doorType @dropChange = 'typeChange'>
      </dropDown>

      <span>宽度：</span>
      <el-input class = 'input' v-model = curWidth type = 'number' placeholder = '请输入宽度'></el-input>

      <span>高度：</span>
      <el-input class = 'input' v-model = curHeight type = 'number' placeholder = '请输入高度'></el-input>

      <span>有无下框：</span>
      <dropDown class = 'typeDrop' :dropData = frameData @dropChange = 'frameChange'>
      </dropDown>

      <span>数量：</span>
      <el-input class = 'input' v-model = curNumber type = 'number' placeholder = '请输入数量'></el-input>

      <el-button class = 'confirm' type = 'primary' @click = 'confirm'>确定</el-button>
    </div>
  </header>

  <main>
    <template v-for = '(data, index) in planData'>
      <div class = 'plan'>
        <template v-for = '(dat, ind) in data'>
          <div class = 'planTitle'  v-if = 'ind < 1'>
            <template v-for = '(da, ins) in dat'>
              <span>{{da.title}}：</span>
              <span>{{da.content}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
            </template>
          </div>

          <template v-for = '(d, i) in dat'>
            <div class = 'planDetail' v-if = 'ind > 0'>
              &nbsp;&nbsp;
              <span>{{d.title}}：</span>
              <span>{{d.content}}</span>
            </div>
          </template>
        </template>

      </div>
    </template>
  </main>
</div>
</template>

<script>
import dropDown from '@/components/dropDown'
import { doubleOpen } from '../../js/doubleOpen'

const doorType = [
  { type: 'singleOpen', title: '单开'},
  { type: 'doubleOpen', title: '对开'}
]

const frameData = [
  { type: 0, title: '无下框'},
  { type: 1, title: '有下框'}
]

export default {
  name: 'index',

  components: { dropDown },

  data () {
    return {
      doorType: doorType,
      curType: doorType[0],
      curWidth: '',
      curHeight: '',
      curNumber: '',
      frameData: frameData,
      underFrame: false,

      planData: [],
    }
  },

  methods: {
    typeChange (value) {
      this.curType = value
    },

    frameChange (value) {
      this.underFrame = value.type;
    },

    confirm () {
      if(this.curWidth != '' && this.curHeight != '' && this.curNumber != '') {
        let plan =
          doubleOpen(this.underFrame,this.curWidth, this.curHeight, this.curNumber)
        this.planData.push(plan)
        console.log(this.planData)
      }

    }
  }
}
</script>

<style scoped>
header {
  width: 100%;
  min-width: 1920px;
  height: auto;
  color: #ffffff;
  border-bottom: 1px solid #dcdfe6;
}
header .content {
  width: 1920px;
  height: auto;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  align-items: center;
}
header .content span {
  color: #000000;
  margin-left: 30px;
}
header .content .input {
  width: 150px;
  height: 40px;
}
header .content .confirm {
  width: 120px;
  height: 40px;
  margin-left: 40px;
}
main {
  width: 100%;
  height: auto;
  padding-top: 20px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  align-items: center;
  -webkit-flex-direction: column;
  flex-direction: column;
}
main .plan {
  /*width: auto;*/
  /*min-width: 200px;*/
  width: 400px;
  height: auto;
  box-sizing: border-box;
  margin-top: 20px;
  border: 1px dashed #000000;
}
main .plan .planTitle {
  width: 100%;
  text-align: center;
  float: left;
  margin-bottom: 20px;
}
main .plan .planDetail {
  width: 50%;
  float: left;
}
</style>



